    font-size: 10px;
    line-height: 10px;
    color: #212121;
    opacity: 0.5;
  }
  /* .parameters-Module-body__select */
  &__select {
    position: relative;
    margin: 8px 0 0;
  }
  /* .parameters-Module-body__select-row */
  &__select-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #F8F8F8;
    border-radius: 2px;
    padding: 8px 12px;
    cursor: pointer;
  }
  /* .parameters-Module-body__select-value */
  &__select-value {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #212121;
    opacity: 0.9;
  }
  /* .parameters-Module-body__select-list */
  &__select-list {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #F8F8F8;
    z-index: 5;
    &.active {
      display: block;
    }
  }
  /* .parameters-Module-body__select-item */
  &__select-item {
    padding: 8px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #212121;
    opacity: 0.9;
    cursor: pointer;
  }
}


.parameters-Module-body {
  /* .parameters-Module-body__checkbox */
  &__checkbox {
    display: flex;
    align-items: center;
    margin: 16px 0 0;
  }
  /* .parameters-Module-body__checkbox-box */
  &__checkbox-box {
    width: 14px;
    height: 14px;
    border: 1px solid #212121;
    border-radius: 2px;
    padding: 1px;
    margin: 0 8px 0 0;
    img {
      display: none;
    }
    &.active {
      img {
        display: block;
      }
    }
  }
  /* .parameters-Module-body__checkbox-name */
  &__checkbox-name {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #212121;
  }
}

.parameters-Module {
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    opacity: 1;
  }
}

.m-16 {
  display: block;
  margin: 16px 0 0;
}